// import stor from './../tools/storage.js';

export default {
  header(head, img) {
    return `
        <img src="${img}" alt="">
        <h2>${head}</h2>
      `;
  },

  leftColumnHeader(head, node) {
    return `
    <div class="catalog-header">
      <div class="catalog-header-title">
        ${node}
        <h2>${head}</h2>
      </div>
    </div>
    `;
  },

  leftColumnGoodsHeaderTrade() {
    return '<thead><tr><th scope="col" class="">#</th><th scope="col" class="w-50"></th><th scope="col"></th><th scope="col"></th><th scope="col"></th><th scope="col"></th></tr></thead>';
  },

  leftColumnGoodsHeaderInventory() {
    return '<thead><tr><th scope="col" class="">#</th><th scope="col" class="w-50"></th><th scope="col"></th><th scope="col"></th></tr></thead>';
  },

  leftColumnGoodsRowTrade(index, name, price, count) {
    return `
      <th scope="row">${index + 1}</th>
      <td>${name}</td>
      <td>${price}</td>
      <td>${count}</td>
      <td>
        <button class="button btn btn-danger mr-1" data-type="add">+1</button>
      </td>
      <td>
        <button class="button btn btn-danger" data-type="card">i</button>
      </td>
    `;
  },

  leftColumnGoodsRowInventory(index, name, price, count) {
    return `
      <td>${index + 1}</td>
      <td>${name}</td>
      <td>${count}</td>
      <td>${price}</td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
      <td></td>
    `;
  },

  rightColumnGoodsPurchase(id, index, name, count, price, sumPurchase, markupGood, priceSell, currMarkup, sumSale) {
    return `
      <th scope="row">${index + 1}</th>
      <td>${name}</td>
      <td data-click="true">
        <span class="w-100" data-click="true">${count}</span>
        <input type="text" class="w-100 d-none" value="${count}" name="count" data-oldValue=${count}">
      </td>
      <td>
        <span data-name="price">
          ${price}
        </span>
      </td>
      <td data-click="true">
        <span class="w-100" data-click="true">
          ${sumPurchase}
        </span>
        <input type="text" class="w-100 d-none" value="${sumPurchase}" name="sumPurchase" data-oldValue="${sumPurchase}">
      </td>
      <td data-click="true">
       <span class="w-100" data-click="true">${currMarkup}%</span>
       <input type="text" class="w-100 d-none" value="${currMarkup}" name="currMarkup" data-oldValue=${currMarkup}">
      </td>
      <td>${markupGood}%</td>
      <td data-click="true">
        <span class="w-100" data-click="true">${priceSell}</span>
        <input type="text" class="w-100 d-none" value="${priceSell}" name="priceSell" data-oldValue=${priceSell}>
      </td>
      <td data-click="true">
        <span class="w-100" data-click="true">${sumSale}</span>
        <input type="text" class="w-100 d-none" value="${sumSale}" name="sumSale" data-oldValue=${sumSale}>
      </td>
    `;
  },

  rightColumnGoodsSale(index, name, count, price) {
    return `
      <th scope="row">${index + 1}</th>
      <td>${name}</td>
      <td>${count}</td>
      <td>${price}</td>
      <td>${Number(price * count).toFixed(2)}</td>
    `;
  },

  rightColumnDiscount(name, count, price, discount) {
    return `
      <th scope="row">#</th>
      <td>${name}</td>
      <td>${discount}%</td>
      <td></td>
      <td>${Number(price * count).toFixed(2)}</td>
    `;
  },

  rightColumnGoodsInventory(index, name, count, oldCount) {
    return `
      <th scope="row">${index + 1}</th>
      <td>${name}</td>
      <td>${oldCount}</td>
      <td>${Number(count - oldCount).toFixed(2)}</td>
      <td>${count}</td>
    `;
  },

};



